import React from 'react';
import './ColumnHeader.scss';
import { FaChevronUp } from 'react-icons/fa';

export default function ColumnHeader({ title, sortable }) {
  const [isSortable, setIsSortable] = React.useState(sortable);
  return (
    <React.Fragment>
      <div className="data-table__col-header">
        <span className="col-header__title">{title}&nbsp;&nbsp;</span>
        <FaChevronUp className="col-header__icon"/>
      </div>
    </React.Fragment>
  );
  
}